.loader {
    background-image: url('./Loading.png');
    width: 90px;
    height: 90px;
    background-repeat: no-repeat;
    background-position: center;
    animation: rotate 1s linear infinite;
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
